import React, {useEffect, useState} from 'react';
import './style.scss';
import Heading from '../Heading';
import Procedures from '../Procedures';
import Service from '../Service';

export default function Price() {

    const [data, setData] = useState<string[][]>([]);

    useEffect(() => {
        fetch('/price.csv').then(response => response.text())
            .then(csvString => {
                const rows = csvString.split('\n');
                const dataArray: string[][] = [];
                for (const row of rows) {
                    const tmp = row.split(",");
                    if (tmp.length === 1 || (!tmp[0].length && !tmp[1].length))
                        continue;
                    dataArray.push(  row.split(","))
                }
                setData(dataArray);
            });

    }, []);

    useEffect(() => {
        // console.log(data);
    }, [data])

   return <div className="price">
        <Heading>
            Прайс-лист
        </Heading>
       <div className="list">
           <Procedures>
               {data.map((row, key) => {
                   return row[1].length === 0 ? <Heading size={'medium'} key={key}>{row[0]}</Heading> : <Service
                       key={key}
                       name={row[1]}
                       price={row[2]}
                   />
               })}
           </Procedures>
       </div>
    </div>
}
