import React from 'react';
import './style.scss';

type Props = {
    children: React.ReactNode;
    size?: 'small' | 'medium' | 'big'
};

export default function Heading({children, size}: Props) {
    return <div className={`heading ${size ? size : ''}`}>
        <div className="content">
            {children}
        </div>
    </div>;
}